 */


 .wp-block-spc-btn-spacial-button {
	position: relative;
	--stickyTop:0px;
	--posY:50svw;
	--posX:50svh;

	--posY_centered:calc(var(--posY) - 50%);
	--posX_centered:calc(var(--posX) - 50%);

	--gridDisplay:block;
	--borderColor:black;
	--borderWidth:2px;
	display: block;
}

@media screen and (max-width:768px) {
	.wp-block-spc-btn-inner-container{
		position: sticky;
		top: var(--stickyTop);
		outline-width: var(--borderWidth);
		outline-color: var(--borderColor);
		outline-style: solid;
		display: block;
	}
}

@media screen and (min-width:768px) {
	
	.wp-block-spc-btn-spacial-button {
		overflow: hidden;
	}
	
	.wp-block-spc-btn-spacial-button::after,
	.wp-block-spc-btn-spacial-button::before{
		display: var(--gridDisplay);
		border-color: var(--borderColors);
		border-style: solid;
		border-width: var(--borderWidth);
		box-sizing: border-box;
		z-index: 3;
		pointer-events: none;
		transition: 0s !important;
	}
	
	.wp-block-spc-btn-spacial-button::before{
		content:'';
		height:var(--innerH);
		width: 100%;
		position: absolute;
		top:0;
		transform:translateY(var(--posY_centered));
		left:0;
		border-right-width: 0px;
		border-left-width: 0px;
	}
	
	.wp-block-spc-btn-spacial-button::after{
		content:'';
		height:100%;
		width: var(--innerW);
		position: absolute;
		top: 0;
		transform:translateX(var(--posX_centered));
		left: 0;
		border-top-width: 0px;
		border-bottom-width: 0px;
	}
	
	.wp-block-spc-btn-inner-container{
		position: absolute;
		top:0px;
		left: 0px;
		transform: translate(
			var(--posX_centered),
			var(--posY_centered)
			);
		pointer-events: none;
	}
	
	.wp-block-spc-btn-inner-container>*:last-child{
		margin-bottom: 0px !important;
	}
}

